/* Login css */
.login {
  background-color: #D4EAF7;
  margin: 0;
  padding: 0;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

}

.container {
  width: 80%;
  flex-grow: 1;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  text-align: left;
  overflow-y: scroll;
  margin-bottom: 50px;
}

.l1 {
  width: 100%;
  height: 108px;
  font-family: 'Inter';
  font-weight: 400;
  font-size: 64px;
  line-height: 1.2;
  text-align: left;
  margin-bottom: 2px;
}
.t2 {
  width: 100%;
  font-family: 'Inter';
  font-weight: normal;
  font-size: 18px;
  text-align: left;
  line-height: 1.2;
  margin-bottom: 2px;
  margin-bottom: 15px;
}

label {
  width: auto;
  height: auto;
  font-family: 'Inter';
  font-weight: normal;
  font-size: 14px;
  line-height: 20px;
  text-align: left;
  margin-bottom: 10px;
  line-height: 10px;
  display:block;
  font-weight: normal;
  color: #3B3C3D;
}
input {
  width: 100%;
  height: 25px;
  margin-bottom: 10px;
  padding: 5px;
}

.fpp {
  width: 100%;
  height: 50px;
  color: #00668C;
  font-size: 14px;
  font-weight: 400;
  text-align: right;
}
.or {
  width: 100%;
  color: #3B3C3D;
  font-size: 14px;
  font-weight: 500;
}
.or label {
  font-size: 16px;
  text-align: center;
}

.lButton {
  width: 100%;
  height: 40px;
  background-color: #00668C;
  color: white;
  border-color: #00668C;
  font-size: 14px;
  font-weight: 500;
}

.Logo2 {
  width: 200px;
  position: absolute;
  padding-top: 10px;
  padding-left: 10px;
  padding-bottom: 0;
}

.Logo2 svg {
  width: 200px; /* Ensures SVG scales with the container */
  height: auto;
}